@import url(https://fonts.googleapis.com/css2?family=Audiowide&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

body{margin:0;padding:0;box-sizing:border-box;background-color:#000;color:#fff}.App{font-family:Montserrat,Droid Sans,sans-serif;height:100vh;margin:.5rem;display:flex;flex-direction:column;grid-gap:.5rem;gap:.5rem}h2,button{font-weight:normal;font-family:Audiowide,Arial,Helvetica,sans-serif;text-align:center;padding:1rem;margin:0;background-color:#ff0;color:#000;border:none}.home-button{font-size:2rem;border-radius:3rem 0 0 0}nav button{font-size:1.5rem;width:100%}nav button:first-child{border-radius:0 0 0 2rem}.button-effect{transition:300ms ease-in-out}.button-effect:hover,.button-effect:focus{background-color:aqua}.selected{background-color:skyblue}p{margin:0}main{flex:100 1 auto;overflow:hidden;display:flex;justify-content:stretch}.decor-box{flex:1 0 1rem;background-color:blue;margin-right:.5rem}article{flex:10 1 auto;overflow:scroll}nav{display:flex;justify-content:space-between;grid-gap:.5rem;gap:.5rem}video{width:100%}
